.profile__add {  
  width: 150px;
  height: 50px;

  background: url(../../../images/Vector_add.svg) no-repeat center;  
  outline: none;
  box-sizing: border-box;
  border: 2px solid #fff;
  border-radius: 2px;
  cursor: pointer;
}

.profile__add:hover {
  opacity: 0.6;
}

@media screen and (max-width: 768px) {
  .profile__add {
    width: 282px;
    background-image: url(../../../images/Vector_add_small.svg);
  }
}